const Evento = (props) => {
    const {url, name, description, logo} = props.evento;
    let imagen = '';

    if(logo.url !== ''){
        imagen = <img src={logo.url} alt={name.text} className="card-img-top" />
    }

    return ( 
        <div className="col-12 mb-4">
            <div className="card">
                <div className="card-image">
                    {imagen}
                </div>
                <div className="card-body">
                    <h3 className="card-title">{name.text}</h3>
                    <p className="card-text">{description.text}</p>
                    <a href={url} className="btn btn-info btn-lg btn-block" target="_blank">Leer Evento</a>
                </div>
            </div>
        </div>
     );
}
 
export default Evento;